
.meetup {
    text-align: left;
    font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
}
h2 {
    font-size: 2.5rem;
    text-align: center;
}
h3 {
    font-size: 2rem;
    color: orangered;
    margin-bottom: 0;
}
.meetup__meta{
    font-size: 1.2rem;
}
.meetup .meetup__meta{
    margin-bottom: 10px;
}
.event{
    padding-bottom: 15px;
    border-bottom: 1px solid #EEE;
}
.event-description {
    padding: 5px 10px;
    background: #F2F2F2;
    border-radius: 3px;
    -webkit-transition: background-color linear 0.2s;
    transition: background-color linear 0.2s;
}
.event-description_expanded{
    background: #FFFFE8;
}
.event-description .event-description__body{
    margin-top: 5px;
    border-top: 1px dashed #CCC;
    padding-top: 10px;
}
.event-description__body {
    overflow-wrap: break-word;
    padding: 20px;
}
.event-description__body img{
    max-width: 100%;
}
.open-on-meetup{
    text-align: right;
    font-size: 1.1rem;
}
.meetup .open-on-meetup{
    margin-bottom: 10px;
}
.meetup address{
    margin: 0;
}
.event-description .btn-link   {
        color: #000;
}
.event-description .btn-link i{
    margin-right: 10px ;
}
.loader{
    text-align: center;
    margin: 20px;
}
.presentations__item{
    list-style: none;
    margin-left: 0;
    padding-left: 0;
}
.presentations__item .fa{
    margin-right: 10px;
}







